/* eslint-disable max-len */
import AppModel from '../Models/AppModel';

import AppView from '../Views/AppView/index';

export default class Api {
  constructor(key = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y', text = 'JS') {
    this.state = {
      q: text,
      apiKey: key,
    };
  }

  async start() {
    const model = new AppModel(this.state);
    const value = await model.request(0);
    const video = await model.nextRequest(value);
    const view = new AppView(video, model.arr);
    view.render();
    Api.slider(model, view);
    Api.setTooltip();
    Api.setButtonListeners(model, view);
  }

  static async nextToken(model, view) {
    const value = await model.request(1);
    const video = await model.nextRequest(value);
    view.setSnippet(video);
    view.setDescription(model.arr);
    view.addElements();
  }

  static setButtonListeners(model, view) {
    const C = document.querySelector('ul');
    document.querySelector('.paginator').addEventListener('click', () => {
      C.classList.add('smooth');
    });
    const previous = document.getElementById('previous');
    const prevPrev = document.getElementById('prev-prev');
    const current = document.getElementById('current');
    const next = document.getElementById('next');
    next.addEventListener('click', () => {
      C.style.setProperty('--i', +next.dataset.page - 1);
      Api.checkNextToken(model, view);
      current.innerText = +next.dataset.page;
      next.dataset.page = `${+next.dataset.page + 1}`;
      previous.dataset.page = `${+previous.dataset.page + 1}`;
      prevPrev.dataset.page = `${+prevPrev.dataset.page + 1}`;
      Api.checkButtonsPrev();
    });
    previous.addEventListener('click', () => {
      C.style.setProperty('--i', +previous.dataset.page - 1);
      current.innerText = +previous.dataset.page;
      next.dataset.page = `${+next.dataset.page - 1}`;
      previous.dataset.page = `${+previous.dataset.page - 1}`;
      prevPrev.dataset.page = `${+prevPrev.dataset.page - 1}`;
      Api.checkButtonsPrev();
    });
    prevPrev.addEventListener('click', () => {
      C.style.setProperty('--i', +prevPrev.dataset.page - 1);
      current.innerText = +prevPrev.dataset.page;
      next.dataset.page = `${+next.dataset.page - 2}`;
      previous.dataset.page = `${+previous.dataset.page - 2}`;
      prevPrev.dataset.page = `${+prevPrev.dataset.page - 2}`;
      Api.checkButtonsPrev();
    });
  }

  static slider(model, view) {
    const C = document.querySelector('ul');
    const previous = document.getElementById('previous');
    const prevPrev = document.getElementById('prev-prev');
    const current = document.getElementById('current');
    const next = document.getElementById('next');
    let N = C.children.length;
    const NF = 30;
    let w;
    let i = 0;
    let x0 = null;
    let locked = false;
    let ini = null;
    let fin = null;
    let rID = null;
    let anf;
    function stopAni() {
      cancelAnimationFrame(rID);
      rID = null;
      current.innerText = +C.style.getPropertyValue('--i') + 1;
      next.dataset.page = +C.style.getPropertyValue('--i') + 2;
      previous.dataset.page = +C.style.getPropertyValue('--i');
      prevPrev.dataset.page = +C.style.getPropertyValue('--i') - 1;
      Api.checkButtonsPrev();
      Api.checkNextToken(model, view);
    }
    function ani(cf = 0) {
      if (!Number.isNaN(ini + (fin - ini) * (cf / anf))) {
        C.classList.remove('smooth');
        C.style.setProperty('--i', ini + (fin - ini) * (cf / anf));
      }
      if (cf === anf) {
        stopAni();
        return;
      }
      let cr = cf;
      rID = requestAnimationFrame(ani.bind(this, cr += 1));
    }
    function unify(e) { return e.changedTouches ? e.changedTouches[0] : e; }
    function lock(e) {
      i = Math.round(+C.style.getPropertyValue('--i'));
      N = C.children.length;
      C.style.setProperty('--n', C.children.length);
      x0 = unify(e).clientX;
      locked = true;
    }
    function drag(e) {
      e.preventDefault();
      N = C.children.length;
      C.style.setProperty('--n', C.children.length);
      if (locked) {
        const dx = unify(e).clientX - x0;
        const f = +(dx / w).toFixed(2);
        C.style.setProperty('--i', i - f);
      }
    }
    function move(e) {
      if (locked) {
        const dx = unify(e).clientX - x0;
        const s = Math.sign(dx);
        let f = +(s * dx / w).toFixed(2);

        ini = i - s * f;

        if ((i > 0 || s < 0) && (i < N - 1 || s > 0) && f > 0.2) {
          i -= s;
          f = 1 - f;
        }
        fin = i;
        anf = Math.round(f * NF);
        ani();
        x0 = null;
        locked = false;
      }
    }
    function size() { w = window.innerWidth; }
    size();
    C.style.setProperty('--n', C.children.length);
    window.addEventListener('resize', size, false);
    C.addEventListener('mousedown', lock, false);
    C.addEventListener('touchstart', lock, false);
    C.addEventListener('mousemove', drag, false);
    C.addEventListener('touchmove', drag, false);
    C.addEventListener('mouseup', move, false);
    C.addEventListener('touchend', move, false);
  }

  static checkNextToken(model, view) {
    const C = document.querySelector('ul');
    let N = C.children.length;
    if (Math.round(N / (+window.getComputedStyle(C).getPropertyValue('--clips-count')) - (+C.style.getPropertyValue('--i'))) === 2) {
      N += 15;
      C.style.setProperty('--n', N);
      Api.nextToken(model, view);
    }
  }

  static tooltipCreate(event) {
    const { target } = event;
    const tooltip = target.querySelector('span');
    tooltip.innerHTML = target.dataset.page;
    tooltip.style.visibility = 'visible';
    const coords = target.getBoundingClientRect();
    let left = coords.left + (target.offsetWidth - tooltip.offsetWidth) / 2;
    if (left < 0) left = 0;

    let top = coords.top - tooltip.offsetHeight;
    if (top < 0) {
      top = coords.top + target.offsetHeight;
    }

    tooltip.style.left = `${left}px`;
    tooltip.style.top = `${top}px`;
  }

  static checkButtonsPrev() {
    const previous = document.getElementById('previous');
    const prevPrev = document.getElementById('prev-prev');
    if (+previous.dataset.page === 1) {
      previous.style.visibility = 'visible';
    } else if (+previous.dataset.page < 1) {
      previous.style.visibility = 'hidden';
    }
    if (+prevPrev.dataset.page === 1) {
      prevPrev.style.visibility = 'visible';
    } else if (+prevPrev.dataset.page < 1) {
      prevPrev.style.visibility = 'hidden';
    }
  }

  static setTooltip() {
    const buttons = document.getElementsByTagName('button');
    let tooltip;
    global.console.log(buttons[0]);
    for (let i = 0; i < buttons.length; i += 1) {
      if (buttons[i].id !== 'current') {
        tooltip = document.createElement('span');
        tooltip.innerHTML = +buttons[i].dataset.page;
        tooltip.className = 'tooltip';
        buttons[i].appendChild(tooltip);
        tooltip.style.visibility = 'hidden';
        if (buttons[i].id !== 'current') {
          buttons[i].addEventListener('mouseover', Api.tooltipCreate);
          buttons[i].addEventListener('mouseout', () => {
            buttons[i].querySelector('span').style.visibility = 'hidden';
          });
        }
      }
    }
  }

  static debounce(fn, delay) {
    let timerId;
    return (...args) => {
      if (timerId) {
        clearTimeout(timerId);
      }
      timerId = setTimeout(() => {
        fn(...args);
        timerId = null;
      }, delay);
    };
  }
}
