export default class AppModel {
  constructor(state) {
    this.state = state;
    this.arr = null;
    this.nextToken = null;
    this.makeUrl = () => {
      const { q, apiKey } = this.state;
      return `https://www.googleapis.com/youtube/v3/search?key=${apiKey}&type=video&part=snippet&maxResults=15&q=${q}`;
    };
    this.checkStatus = (result) => {
      if (Number(result.status) >= 200 && Number(result.status) < 300) {
        return result;
      }
      const err = {
        status: result.status,
        ok: false,
        statusText: result.statusText,
      };
      throw err;
    };
    this.parseJSON = (response) => {
      if (response.status === 204 || response.status === 205) {
        return null;
      }
      return response.json();
    };
  }

  static extractclips(clips) {
    return clips.items.map(clip => clip.snippet);
  }

  async request(param) {
    let url;
    try {
      if (param === 0) {
        url = this.makeUrl();
      } else {
        url = `${this.makeUrl()}&pageToken=${this.nextToken}`;
      }
      const res = await fetch(url);
      this.checkStatus(res);
      const value = await this.parseJSON(res);
      this.nextToken = value.nextPageToken;
      this.arr = value.items.map(item => item.snippet.description);
      return value.items.map(item => item.id.videoId).join(',');
    } catch (error) {
      throw error;
    }
  }


  async nextRequest(data) {
    try {
      const res = await fetch(`https://www.googleapis.com/youtube/v3/videos?key=${this.state.apiKey}&id=${data}&part=snippet,statistics`);
      this.checkStatus(res);
      const value = await this.parseJSON(res);
      return value;
    } catch (error) {
      throw error;
    }
  }
}
