import Snippet from '../SnippetView/index';

export default class AppView {
  constructor(snippets, descriptions) {
    this.snippets = snippets;
    this.descriptions = descriptions;
  }

  setSnippet(data) {
    this.snippets = data;
  }

  setDescription(desc) {
    this.descriptions = desc;
  }

  render() {
    const content = document.createElement('ul');
    let i = -1;
    content.className = 'smooth';
    content.innerHTML = this.snippets.items.map((snippet) => {
      i += 1;
      return `<li>${Snippet.createSnippet(snippet, this.descriptions[i])}</li>`;
    }).join('');
    document.body.appendChild(content);
    const div = document.createElement('div');
    div.className = 'paginator';
    document.body.append(div);
    AppView.pagesView();
  }

  addElements() {
    let i = -1;
    const ul = document.querySelector('ul');
    let k = '';
    this.snippets.items.map((snippet) => {
      k = document.createElement('li');
      i += 1;
      k.innerHTML = `${Snippet.createSnippet(snippet, this.descriptions[i])}`;
      ul.appendChild(k);
      return k;
    });
  }

  static pagesView() {
    const obj = {
      2: 'current',
      1: 'previous',
      3: 'next',
      0: 'prev-prev',
    };
    const paginator = document.querySelector('.paginator');
    let page = '';
    for (let i = 0; i < 4; i += 1) {
      if (obj[i] !== 'current') {
        page += `<button data-page=${i - 1} id=${obj[i]}>${i - 1}</button>`;
      } else {
        page += `<button id=${obj[i]}>${i - 1}</button>`;
      }
    }
    paginator.innerHTML = page;
    document.getElementById('previous').style.visibility = 'hidden';
    document.getElementById('prev-prev').style.visibility = 'hidden';
  }
}
