import Api from './Controllers/Api';
import AppInput from './Views/Input/AppInput';

AppInput.input();

function clear() {
  const children = document.body.childNodes;
  for (let i = 0; i < children.length; i += 1) {
    if (children[i].nodeName !== 'INPUT') {
      document.body.removeChild(children[i]);
    }
  }
}

document.getElementById('search').addEventListener('input', Api.debounce(() => {
  clear();
  if (document.getElementById('search').value !== '') {
    const api = new Api('AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y', document.getElementById('search').value);
    api.start();
  }
}, 1500));
