export default class Snippet {
  static createSnippet(snippet, description) {
    const content = document.createElement('div');
    content.classList.add('wrapper');
    content.innerHTML = `<div class="title"><a href=${`https://www.youtube.com/watch?v=${snippet.id}`} target="_blank" >${snippet.snippet.title}</a></div>
      <img src=${snippet.snippet.thumbnails.high.url}>
      <div class="inf">
          <p><i class="fas fa-user"></i>${snippet.snippet.channelTitle}</p>
      </div>
      <div class="inf">
          <p><i class="far fa-calendar-alt"></i>${Snippet.converDate(snippet.snippet.publishedAt)}</p>
      </div>
      <div class="inf">
          <p><i class="fas fa-eye"></i>${snippet.statistics.viewCount}</p>
      </div>
      <p class="text">
       ${description}
      </p>`;
    return `<div class="wrapper">${content.innerHTML}</div>`;
  }

  static converDate(string) {
    const date = new Date(string);
    let month = date.getMonth() + 1;
    let day = date.getDate();
    if (day < 10) {
      day = `0${day}`;
    }
    if (month < 10) {
      month = `0${month}`;
    }
    return `${date.getFullYear()}-${month}-${day}`;
  }
}
